.popup__container-avatar {
    background-color: #fff;
    min-width: 430px;
    min-height: 272px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    position: relative;
    margin-left: auto;
    margin-right: auto;
  }
  
  @media screen and (max-width: 900px) {
    .popup__container-avatar {
      min-width: 282px;
      min-height: 322px;
    }
  } 